.directions {
  display: flex;
  justify-content: center;
  position: relative;
  bottom: 10px;
  font-family: Georgia, "Times New Roman", Times, serif;
}
h1 {
  font-family: Georgia, "Times New Roman", Times, serif;
  display: flex;
  justify-content: center;
}

.score-container {
  display: flex;
  margin: 0 auto;
  justify-content: center;
}
.score {
  background: lightgreen;
  padding: 5px;
  border-radius: 6px;
}

.imgcontainer {
  display: grid;
  grid-template-columns: 2fr 2fr 2fr 1fr;
  margin: 32px;
}

.imagelist {
  position: relative;
  width: 163px;
  margin: 16px;
}
.imagelist img {
  width: 200px;
  height: 200px;
  box-shadow: 10px 10px 5px grey;
  border: solid;
  border-radius: 10px;
}
.overlay {
  position: absolute;
  bottom: 30px;
  background: rgb(0, 0, 0);
  background: rgba(0, 0, 0, 0.5);
  color: #f1f1f1;
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  color: white;
  font-size: 20px;
  padding: 20px;
  text-align: center;
}
.imagelist:hover .overlay {
  opacity: 1;
}

.header {
  padding: 50px;
  background: #22aeff;
  min-height: 100%;
  min-width: 92%;
  border-radius: 10px;
}
.footer {
  padding: 50px;
  background: #22aeff;
  border-radius: 10px;
  min-width: 65%;
}
